body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



nav{
  padding: 0 1rem;
  background: red;
}

.content{
  min-height: calc(100vh - 70px - 64px);
}

.card{
  display: flex;
  flex-direction: column;
}

.card .card-content{
  flex-grow: 1;
}

.goods{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
}

.search-btn{
  position: absolute;
  top: 0;
  right: 0.5rem;
}

label:not(:last-child){
  margin-right: 2rem;
}

.cart{
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  cursor: pointer;
  z-index: 5;
  padding: 1rem;
}

@media (min-width: 767px) {
  .cart{
    top: 5rem;
    bottom: unset;
  }
}

.basket-list{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  width: 50rem;
  max-width: 95%;
  box-shadow: inset -1px 3px 8px 5px #1f87ff, 2px 5px 16px 0px #0b325e, 5px 5x 15px 5px rgba(0, 0, 0, 0);
  animation: show 500ms ease-in-out;
}

.basket-close{
  cursor: pointer;
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  color: white;
}

.basket-delete{
  cursor: pointer;
}

.basket-quantity{
  cursor: pointer;
  vertical-align: middle;
  color: #26a69a;
}

.btn-buy{
  color: #26a69a !important;
  border:none;
  font-weight: bold;
  padding: 4px 5px;
}

#toast-container{
  top: 2.7rem !important;
  animation: fade-in 250ms ease-in-out;
}

@keyframes fade-in {
  from{
    opacity: 0;
    transform: translateY(2rem);
  }
  to{
    opacity: 1;
    transform: translateY(0rem);
  }
}

@keyframes show {
  from{
    top:70%;
    opacity: 0 ;
  }
  to{
    top:50%;
    opacity: 1;
  }
}